#boardTable{
  border-collapse: separate; 
  border-spacing: 0 5px;
  width: 100%;
  margin-bottom: 15px;
}
#boardTable tr{
  text-align: center;
}
#boardTable tr:nth-child(even) td{
  border-bottom: 1px solid gray;
  padding-bottom: 30px;
}
td {
  padding: 15px 0;
}
/* #boardTable.emptyList{
  text-align: center;
} */
#boardTable td.subject{
  text-align: left;
}
tbody {
  border-bottom: 1px solid gray;
}
#btn{height: 100px;}